#homePage {
  margin-top: 40px;
  text-align: center;
  min-height: 350px;
  margin-bottom: 40px;
}

#homePage h1 {
  font-size: 35px;
  color: #ffffcb;
}
.home-page-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.photo {
  height: 400px;
  width: 450px;
  margin-right: 50px;
  margin-bottom: 30px;
  border-radius: 25px;
  background-image: url("../images/woman.jpg");
  background-size: cover;
  background-position: center;
}

.description {
  align-self: flex-start;
  text-align: left;
  height: 400px;
  width: 300px;
  color: white;
  margin-bottom: 50px;
}
